import { render, useState, useEffect } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';
import { Card, CardBody, CardHeader, Spinner } from '@wordpress/components';
import '../css/dashboard.css';

const Dashboard = () => {
	const [stats, setStats] = useState(null);
	const [loading, setLoading] = useState(true);
	const [error, setError] = useState(null);

	useEffect(() => {
		loadStats();
	}, []);

	const loadStats = async () => {
		try {
			setLoading(true);
			const response = await apiFetch({
				path: '/ai-media/v1/stats',
				method: 'GET',
			});
			setStats(response);
			setError(null);
		} catch (err) {
			setError(err.message);
		} finally {
			setLoading(false);
		}
	};

	if (loading) {
		return (
			<div className="ai-media-dashboard-loading">
				<Spinner />
				<p>Loading dashboard...</p>
			</div>
		);
	}

	if (error) {
		return (
			<div className="ai-media-dashboard-error">
				<p>Error loading dashboard: {error}</p>
				<button onClick={loadStats} className="button">
					Retry
				</button>
			</div>
		);
	}

	const statItems = [
		{ label: 'Total Images', value: stats?.total || 0, icon: 'images-alt2' },
		{ label: 'Pending', value: stats?.pending || 0, icon: 'clock' },
		{ label: 'Processing', value: stats?.processing || 0, icon: 'update' },
		{ label: 'Needs Review', value: stats?.needs_review || 0, icon: 'visibility' },
		{ label: 'Approved', value: stats?.approved || 0, icon: 'yes-alt' },
		{ label: 'Failed', value: stats?.failed || 0, icon: 'warning' },
	];

	return (
		<div className="ai-media-dashboard">
			<div className="ai-media-dashboard-header">
				<h2>Dashboard Overview</h2>
				<button onClick={loadStats} className="button">
					<span className="dashicons dashicons-update"></span> Refresh
				</button>
			</div>

			<div className="ai-media-stats-grid">
				{statItems.map((item, index) => (
					<Card key={index} className="ai-media-stat-card">
						<CardBody>
							<div className="ai-media-stat-icon">
								<span className={`dashicons dashicons-${item.icon}`}></span>
							</div>
							<div className="ai-media-stat-content">
								<div className="ai-media-stat-value">{item.value}</div>
								<div className="ai-media-stat-label">{item.label}</div>
							</div>
						</CardBody>
					</Card>
				))}
			</div>

			<div className="ai-media-dashboard-details">
				<Card>
					<CardHeader>
						<h3>Performance Metrics</h3>
					</CardHeader>
					<CardBody>
						<div className="ai-media-metrics">
							<div className="ai-media-metric">
								<span className="label">Processed Today:</span>
								<span className="value">{stats?.processed_today || 0}</span>
							</div>
							<div className="ai-media-metric">
								<span className="label">Average Score:</span>
								<span className="value">
									{stats?.avg_score ? (stats.avg_score * 100).toFixed(0) + '%' : 'N/A'}
								</span>
							</div>
							<div className="ai-media-metric">
								<span className="label">Total Cost:</span>
								<span className="value">
									${stats?.total_cost ? (stats.total_cost / 100).toFixed(2) : '0.00'}
								</span>
							</div>
						</div>
					</CardBody>
				</Card>
			</div>
		</div>
	);
};

// Initialize dashboard when DOM is ready
document.addEventListener('DOMContentLoaded', () => {
	const root = document.getElementById('ai-media-dashboard-root');
	if (root) {
		render(<Dashboard />, root);
	}
});
